class OAuth2Manager {
    constructor(config) {
        const that = this;
        that.authConfig = config;
        that.oauth2Token = {};
        that.preserveOAuth2Config();
    }
    clearOff () {
        const that = this;
        setTimeout(function() {
            delete that.refreshTokenPromise;
            if(that._oauth2RefreshTokenScheduled) {
                window.clearTimeout(that._oauth2RefreshTokenScheduled);
            }
            delete that.fetchTokenPromise;
        }, 10);
    }
    refreshOAuth2Token () {
        const that = this;
        const oauth2Config = that.authConfig;
        if(that.refreshTokenPromise) return that.refreshTokenPromise;
        if(that._oauth2RefreshTokenScheduled) {
            window.clearTimeout(that._oauth2RefreshTokenScheduled);
            delete that._oauth2RefreshTokenScheduled;
        }
        if(!isNaN(oauth2Config.expires_in) && (typeof oauth2Config.refreshToken === "function")) {
            that._oauth2RefreshTokenScheduled = setTimeout(function() {
                that.refreshTokenPromise = oauth2Config.refreshToken();
                that.refreshTokenPromise.then(function(data) {
                    that.clearOff();
                    that.storeOAuth2Token(data);
                    that.refreshOAuth2Token();
                }, function() {
                    that.clearOff();
                    that.fetchToken();
                });
            }, Math.max(oauth2Config.expires_in, oauth2Config.expires_in - 100));
        }
    }
    fetchOAuth2Token () {
        const that = this;
        const oauth2Config = that.authConfig;
        if(that.fetchTokenPromise) return that.fetchTokenPromise;
        if(typeof oauth2Config.fetchToken === "function") {
            that.fetchTokenPromise = oauth2Config.fetchToken();
            return that.fetchTokenPromise.then(function(data) {
                that.clearOff();
                that.storeOAuth2Token(data);
                that.refreshOAuth2Token();
            }, function() {
                that.clearOff();
                that.fetchToken();
            });
        }
    }
    storeOAuth2Token (data) {
        const that = this;
        that.oauth2Token = {
            accessToken: data.accessToken || data.access_token,
            expires_in: data.expiresIn || data.expires_in
        };
    }
    get OAuth2Token() {
        return Object.assign({}, this.oauth2Token);
    }
    preserveOAuth2Config() {
        const that = this;
        const oauth2Config = that.authConfig;
        if(oauth2Config.accessToken) {
            that.storeOAuth2Token(oauth2Config);
            that.refreshOAuth2Token();
        } else {
            that.fetchOAuth2Token();
        }
    }
    getOAuth2TokenInfo () {
        const that = this;
        return new Promise(function(resolve, reject) {
            let tokenFetcher = that.refreshTokenPromise || that.fetchTokenPromise;
            if(tokenFetcher) {
                tokenFetcher.then(function() {
                    resolve(that.OAuth2Token);
                }, function() {
                    reject("Error while fetching the accessToken");
                });
            } else if (that.oauth2Token.accessToken) {
                resolve(that.oauth2Token);
            } else {
                resolve();
            }
        });
    }
    set accessToken (val) {
        if(val) {
            this.oauth2Token.accessToken = val;
        }
    }
    set expires_in (val) {
        if(!isNaN(val)) {
            this.oauth2Token.expires_in = val;
        }
    }
    set fetchToken (fun) {
        const that = this;
        if(typeof fun === "function") {
            that.authConfig.fetchToken = fun;
            that.fetchOAuth2Token();
        } else {
            throw "fetchToken value must be a function";
        }
    }
    set refreshToken (fun) {
        const that = this;
        if(typeof fun === "function") {
            that.authConfig.refreshToken = fun;
            that.refreshOAuth2Token();
        } else {
            throw "refreshToken value must be a function";
        }
    }
    get type () {
        return "oauth2";
    }
}

class apiKeyManager {
    constructor (config) {
        this.authConfig = config;
    }
    getAPIKeyInfo () {
        const that = this;
        return new Promise(function(resolve, reject) {
            let apiKey = that.authConfig;
            if(apiKey) {
                resolve(apiKey);
            } else {
                reject("No APIkey Found");
            }
        });
    }
    get type () {
        return "apiKey";
    }
}

class basicAuthManager {
    constructor (config) {
        this.authConfig = config;
    }
    getBasicAuthInfo () {
        const that = this;
        return new Promise(function(resolve, reject) {
            let basicAuth = that.authConfig;
            if(basicAuth) {
                resolve(basicAuth);
            } else {
                reject("No Basic auth info Found");
            }
        });
    }
    get type () {
        return "basic";
    }
}


class AuthManager {
    constructor (config) {
        const that = this;
        that._authentications = config;
        that.authentications = {};
        that.init();
    }
    init() {
        const that = this;
        for(let authName in that._authentications) {
            let authConfig = that._authentications[authName];
            let manager = null;
            switch(authConfig.type) {
                case "oauth2":
                    manager = new OAuth2Manager(authConfig);
                break;
                case "apiKey":
                case "basic":
                    manager = Object.assign({}, authConfig);
                    manager = Object.assign({}, authConfig);
                break;
                default:
                    throw new Error('Unknown authentication type: ' + auth.type);
            }
            that.authentications[authName] = manager;
        }
    }
}

